import {
  AppRoot,
  SplitLayout,
  SplitCol,
  View,
  Panel,
  PanelHeader,
  usePlatform,
} from '@vkontakte/vkui';

const ErrorBlock = () => {
  const platform = usePlatform();

  return (
    <AppRoot>
      <SplitLayout header={platform !== 'vkcom' && <PanelHeader delimiter="none" />}>
        <SplitCol autoSpaced>
          <View activePanel="main">
            <Panel id="main">
              <PanelHeader></PanelHeader>
              <div className='error-block'>
                <div className="error-block__inner">
                  <h4 className="error-block__title"> :&#40;</h4>
                  <p className="error-block__text">    </p>
                  <p className="error-block__text">  </p>
                </div>
              </div>
            </Panel>
          </View>
        </SplitCol>
      </SplitLayout>
    </AppRoot>
  )
}

export default ErrorBlock
